header {
  margin: auto;
  width: fit-content;
  text-align: center;
}

header img {
  position: fixed;
  left: 3rem;
  top: 2rem;
  width: 5rem;
  z-index: 500;
  cursor: grab;
}

.title {
  font-size: 3rem;
}

.subtitle {
  font-size: 2rem;
}

@media screen and (max-width: 550px) {
  header img {
    left: 2rem;
    width: 4rem;
  }
}
